@include tablet {
    .iconos-nosotros {
        @include grid(3, 2rem);
    }
}

.icono {
    text-align: center;
    img {
        height: 10rem;
        width: 10rem;
        display: block;
        margin: 0 auto;
    }
    h3 {
        text-transform: uppercase;
    }
}

li {
    .icono-anuncio {
        display: flex;
        align-items: center;
        width: 5rem;
        height: 5rem;
    }
}